// This file contains all necessary items for the GameButtons Component which accounts for the top buttons for each game

// Created before CIS 482

import React from "react";
import ShuffleIcon from "../images/icons/shuffle.png";
import HelpIcon from "../images/icons/help.png";
import ExitIcon from "../images/icons/exit.png";
import "../css/GameButtons.css";

class GameButtons extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      showing: this.props.showing,
      modalClassName: "modal-hidden",
      mode: this.props.mode
    };
  }

  // toggles the help on or off
  toggleHelp = () => {
    var classNameToBe;

    if (this.state.modalClassName === "modal-hidden") {
      classNameToBe = "modal-showing";
    } else {
      classNameToBe = "modal-hidden";
    }

    this.setState({
      showing: !this.state.showing,
      modalClassName: classNameToBe
    });
  }

  //handles click of exit button
  handleExit = () => {
    this.props.goToMainMenu()
  }

  //handles click of shuffle button
  handleShuffle = () => {
    this.props.shuffle()
  }

  render() {
    return (
      <div className="button-modal-container">

        {/* Darkened Background*/}
        <div className={this.state.modalClassName}></div>

        {/* Sandbox Buttons */}
        {this.state.mode === "sandbox" && <div>
        <img src={ShuffleIcon} style={{zIndex:2999}} draggable={false} className="icon" alt="shuffle icon" id="shuffle-icon" onClick={this.handleShuffle}/>
        <img src={HelpIcon} style={{zIndex:2999}} draggable={false} className="icon" alt="help icon" id="help-icon" onClick={this.toggleHelp}/>
        <img src={ExitIcon} style={{zIndex:2999}} draggable={false} className="icon" alt="exit icon" id="exit-icon" onClick={this.handleExit}/>


        {this.state.showing &&
          <div id="header-and-body">

            <div id="header">
              <p id='header-text'>{this.props.helpText.header}</p>
            </div>

            <div id="body">
              <p id='body-text'>{this.props.helpText.body}</p>
            </div>

            <div id="close-button-container">
                <div id="close-button" onClick={this.toggleHelp}>
                  <p id="close-button-text">Close</p>
                </div>
            </div>
          </div>
        }
        </div>
      }

      {/* Game Buttons */}
      {this.state.mode === "game" && <div>
      <img src={HelpIcon} style={{zIndex:2999}} draggable={false} className="icon" alt="help icon" id="help-icon" onClick={this.toggleHelp}/>
      <img src={ExitIcon} style={{zIndex:2999}} draggable={false} className="icon" alt="exit icon" id="exit-icon" onClick={this.handleExit}/>

      {/* Header & Body*/}
      {this.state.showing &&
        <div id="header-and-body">

          <div id="header">
            <p id='header-text'>{this.props.helpText.header}</p>
          </div>

          <div id="body">
            <p id='body-text'>{this.props.helpText.body}</p>
          </div>

          <div id="close-button-container">
              <div id="close-button" onClick={this.toggleHelp}>
                <p id="close-button-text">Close</p>
              </div>
          </div>

        </div>
      }
      </div>
    }

    {/* Menu Buttons */}
    {this.state.mode === "menu" && <div>
    <img src={ExitIcon} style={{zIndex:2999}} draggable={false} className="icon" alt="exit icon" id="exit-icon" onClick={this.handleExit}/>
    </div>
    }

  {/* Close Modal Button */}
  </div>
    );
  }
}

export default GameButtons;
